.searchbar {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 1100;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;

  color: #fff;

  background-color: #3f51b5;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.searchForm {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;

  background-color: #fff;
  border-radius: 3px;

  overflow: hidden;
}

.searchFormBtn {
  position: relative;
  /* display: inline-block; */
  width: 48px;
  height: 48px;
  opacity: 0.6;

  border: 0;

  cursor: pointer;
  outline: none;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.searchFormBtn:hover {
  opacity: 1;
}

.searchFormBtnIcon {
  position: absolute;
  width: 80%;
  height: auto;
  transform: translate(-50%, -50%);
}

.searchFormInput {
  /* display: inline-block; */
  width: 100%;
  padding-left: 4px;
  padding-right: 4px;

  font: inherit;
  font-size: 20px;

  border: none;
  outline: none;
}

.searchFormInput::placeholder {
  font: inherit;
  font-size: 18px;
}
